<template>
  <div style="display: flex;flex-direction: column;">
    <div class="lightColor" style="height:100px;display: flex;flex-direction: column;align-items: flex-start;">
      <h1>灯光颜色</h1>
      <el-image 
        src='./img/8General/Color/colorbar.png' 
        alt="Color Picker"
        @click="getPixelColor($event)" 
        ref="imageElement">
      </el-image>
    </div>
    
    <div class="colorPreview" style=" height:200px;display: flex;flex-direction: column;align-items: flex-start;">
      <h1>颜色预览</h1>
      <div class="left" style="margin-top: 20px; height:100px;display: flex;flex-direction: row;align-items: flex-start;">
        <div class="box" :style="{backgroundColor:boxColor}"></div>

        <div class="left" style="height:100px;display: flex;flex-direction: column;align-items: flex-start;">
          <div style="height:30px;display: flex;flex-direction: row;align-items: flex-start;">
            <span>R:</span>
            <el-input-number v-model="colorR" @change="colorRChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
          <div style="height:30px;display: flex;flex-direction: row;align-items: flex-start;">
            <span>G:</span>
            <el-input-number v-model="colorG" @change="colorGChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
          <div style="height:30px;display: flex;flex-direction: row;align-items: flex-start;">
            <span>B:</span>
            <el-input-number v-model="colorB" @change="colorBChange" :min="0" :max="255" size="mini"></el-input-number>
          </div>
        </div>
      </div>
    </div>
    <div class="colorPreset" style="height:100px;display: flex;flex-direction: column;align-items: flex-start;">
      <h1>预设颜色</h1>
      <div style="height:40px;display: flex;flex-direction: row; justify-content: center;flex-wrap: wrap;">
        <el-image 
          v-for="(item,index) in 14"
          :key="index"
          :src='`./img/8General/Color/color${item}.png`' 
          alt="Color Picker"
          @click="getPixelColor($event)" 
          ref="imageElement"
          style="height: 20px;width: 20px;margin-right: 30px;margin-top: 20px;">
        </el-image>
      </div>

    </div>
  </div>



</template>

<script>

  export default {
      data() {
        return {
          boxColor:'#000',
          colorR:0,
          colorG:0,
          colorB:0,
        };
      },
      methods: {
        getPixelColor(event) {
          const imgElement = event.target;
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
    
          canvas.width = imgElement.naturalWidth;
          canvas.height = imgElement.naturalHeight;
          ctx.drawImage(imgElement, 0, 0);
    
          const pixelData = ctx.getImageData(
            event.offsetX,
            event.offsetY,
            1,
            1
          ).data;
          
          const rgb = `rgb(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]})`;
          this.boxColor = rgb;
          this.colorR = pixelData[0];
          this.colorG = pixelData[1];
          this.colorB = pixelData[2];
        },
        colorRChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        },
        colorGChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        },
        colorBChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        }
      }
    };
</script>

<style lang="less" scoped>
.box {
  border: 0px solid #000; /* 边框样式 */
  width: 100px;           /* 宽度 */
  height: 100px;          /* 高度 */
}
</style>