<template>
    <div>
        <h1>我是Custom</h1>
        <div v-for="(item,index) in keys" :key="index">
          <el-cascader 
            v-show="loadFinish" 
            :options="keyOptions" 
            :show-all-levels="false" 
            placeholder=""
            @change="((val)=>{selectChange(val,index)})">
          </el-cascader>
        </div>
        <el-dialog
          title="提示"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
          <span>这是一段信息</span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
  import HIDHandle from '../components/HIDHandle'

  export default {
    data() {
      return {
        loadFinish:false,
        dialogVisible: false,
        keyOptions:[],
        keys:[]
      }
    },
    methods: {
      async selectChange(value,index) {       
        let data = [];
        data[0] = Number.parseInt(value[0]);
        let tmp = Number.parseInt(value[1]);
        data[1] = tmp >> 8;
        data[2] = tmp & 0xFF;
        console.log(value,index,data);
        await HIDHandle.setKeyFunction(data,index);
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    },
    mounted() {
      this.$bus.$on('getLanFiles',value => {
        this.keyOptions = value[1].KeysFun;
        console.log('keyOptions',this.keyOptions);
      });

      this.$bus.$on('getDeviceInfo',value => {
        this.loadFinish = true;
        this.keys = value.Keys;
        console.log('keys',this.keys);
      });
    },
    beforeDestroy() {
      this.$bus.$off('getLanFiles');
      this.$bus.$off('getDeviceInfo');
    }
  }
</script>

