<template>
  <div>
    <span>回报率</span>
    <div class="reportRate">
      <el-radio 
        v-model="reportRate" 
        v-for="(item,index) in reportRates"
        :key="index"
        :label="item.value" 
        border
        @input="setReportRate()">
        {{ item.label }}
        </el-radio>
    </div>
  </div>
</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    data() {
      return {
        reportRate : 8,
        reportRates: [
          {
            value : 8,
            label : '125Hz'
          },
          {
            value : 4,
            label : '250Hz'
          },
          {
            value : 2,
            label : '500Hz'
          },
          {
            value : 1,
            label : '1000Hz'
          },
          {
            value : 16,
            label : '2000Hz'
          },
          {
            value : 32,
            label : '4000Hz'
          },    
          {
            value : 64,
            label : '8000Hz'
          },         
        ]
      }
    },
    methods: {
      async setReportRate() {
        console.log('report',this.reportRate);
        await HIDHandle.setReport(this.reportRate);
      }
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  display: flex;
  align-items: flex-start;
  margin-left:20px;
}

.reportRate {
  display: flex;
  flex-direction: row;
  margin-top: 40px;
}

.el-radio {
  color:white;
}

/deep/.el-radio .el-radio__input {
    display: none;
    color:white;
}

.el-radio.is-bordered {
  border-radius: 10px;
  margin-left: 50px;
}

.el-radio.is-bordered.is-checked {
  border-color: white;
  background-color: #FF7F00;
}

/deep/.el-radio .el-radio__label {
  color:white;
}
</style>