<template>
  <div style="display: flex;flex-direction: column;">
    <div class="lightMode" style="height:60px;display: flex;align-items: center;">
      <h1>灯光模式</h1>
      <select v-show="loadFinish" name="lightMode" style="top:15vh;position: absolute; font-size: 12px; width: 250px; height: 35px; text-align: center;">
        <option
          style="font-size: 16px"
          v-for="(item,index) in lightModes"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
      </select>
    </div>

    <div class="brightness" style="height:60px;display: flex;align-items: center;">
      <h1>亮度</h1>
      <el-slider
        :min="1" 
        :max="10"
        v-model="brightness" 
        style="top:8vh;position: absolute; width: 250px; height: 20px;">
      </el-slider>
    </div>

    <div class="speed" style="height:60px;display: flex;align-items: center;">
      <h1>速度</h1>
      <el-slider 
        v-model="speed" 
        :min="1" 
        :max="10"
        style="top:8vh;position: absolute; width: 250px; height: 1px;">
      </el-slider>
    </div>

    <div class="lightOff" style="height:60px;display: flex;align-items: center;">
      <span style="font-size: 14px;">移动式灯光关闭</span>
      <el-switch
        v-model="lightState"
        style="margin-left: 100px;"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </div>

    <div class="lightOffTime" style="height:60px;display: flex;align-items: center;">
      <span style="font-size: 14px;">放停后灯光关闭时间</span>
      <select v-show="loadFinish" name="lightOffTime" style="margin-left:50px;position: absolute; font-size: 12px; width: 80px; height: 25px; text-align: center;">
        <option
        style="font-size: 16px"
        v-for="(item,index) in lightOffTimes"
        :key="index" 
        :value="item.value">
        {{ item.option }}
        </option>
      </select>
    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        lightModes:[],
        brightness : 3,
        speed: 7,
        lightState: false,
        lightOffTimes:[],
        loadFinish:false,
      }
    },
    mounted() {
      this.$bus.$on('getLanFiles',value => {
        this.lightModes = value[1].LightModeOption;
        this.lightOffTimes = value[1].LightOffTimeOption;
        console.log('Light',this.lightModes);
        this.loadFinish = true;
      });
    },
    beforeDestroy() {
      this.$bus.$off('getLanFiles');
    }
  }
</script>

<style lang="less" scoped>
  .el-row {
    margin-bottom: 20px;
    margin-left: 20px;
    text-align: left;
    &:last-child {
      margin-bottom: 0;
    }
  }
.lightMode {
  position: absolute;
  top:6vh;
}

.brightness {
  position: absolute;
  top:20vh;
}

.speed {
  position: absolute;
  top:32vh;
}

.lightOff {
  position: absolute;
  top:48vh;
}

.lightOffTime {
  position: absolute;
  top:55vh;
}

h3 {
  font-size: 12px;
}
</style>