<template>
  <div style="display: flex; flex-direction: row;align-items: center;">
    <label 
      class="radio-button"
      v-for="(option,index) in options"
      :key="index">
      <input
        :value="option.value"
        type="radio"
        :name="name"
        :checked="option.value === modelValue"
        @click="selectOption(index)"
      />
      <span class="radio-button-inner">{{ option.value }}</span>
	    <input type="color" class="radio-button-color" v-model="option.color" @click="selectColor(index)" @input="ChangeColor(option)">
    </label>
  </div>
</template>

<script>

  export default {
      props: {
      options: {
        type: Array,
      },
      modelValue: {
        type: [String, Number],
        default: ''
      },
      name: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        currentIndex:0,
      }
    },
    methods: {
      selectOption(value) {
        this.$bus.$emit('DpiUpdateIndex', value);
      },
      selectColor(index) {
        this.currentIndex = index;
      },
      ChangeColor(value) {
        var color = [];
        color[0] = this.currentIndex;
        color[1] = value.color;
        this.$bus.$emit('DpiUpdateColor', color);
      }
    }
  }
</script>

<style lang="less" scoped>
.radio-button {
    height: 100px;
    width: 100px;
    border: 2px solid #999;
    border-radius: 3%;
    margin-right: 50px;
    display: flex;
	  flex-direction: column; 
    position: relative;
  }
 
  .radio-button input[type="radio"] {
    opacity: 0;
    position: absolute;
  }
	
  .radio-button input[type="radio"]:checked + .radio-button-inner {
    background-color: #FF7F00;
  }
	
  .radio-button-inner {
    height: 100%;
    width: 100%;
    border-radius: 0%;
    background-color: #fff;
    color:black;
    transition: background-color 0.3s;
    display: flex;
    align-items:center;
    justify-content: center; /* 水平居中，如果需要的话 */  
  }
	
	.radio-button-color {
		height: 20%;
		width: 100%;
		border-radius: 2%;
		padding:0px;
	}
	
	input[type="color"]::-webkit-color-swatch-wrapper {
        padding: 0px; /* 水平和垂直内边距 */
    }
	input[type="color"]::-webkit-color-swatch {
        border: 1px solid #fff; /* 可以设置边框样式 */
        padding: 0px; /* 颜色方块内的内边距 */
    }
</style>