<template>
  <div v-show="loadFinish" style="display: flex;flex-direction: column;">
    <div style="height:60px;display: flex;align-items: center;">
      <span>DPI级数</span>
      <select v-model="maxDPI" name="dpi" @change="changeMaxPDI">
        <option 
        v-for="(item,index) in dpis" 
        :key="index"
        :value="index">
        {{ index+1 }}
      </option>
      </select>
    </div>
    <div style="width: 100%; height:100px;display: flex;align-items: center;">
      <el-slider 
        v-model="dpiValue" 
        show-input
        :min="50"
        :max="26000"
        :step="50"
        @change="ChangeDPIValue">
      </el-slider>
    </div>
    <div style="width: 100%; height:150px; display: flex; flex-direction: row;justify-content: center;">
      <!-- <el-radio 
        v-for="(item,index) in dpis"
        :key="index"
        v-model="dpiIndex" 
        :label="index" 
        style="color:white;"
        @input="ChangeDPIIndex"
        border>
        {{ item.value }}
        </el-radio> -->
        <dpi-radio :options="dpis" name="DPIRadioGroup"/>
    </div>

    <!-- <div style="background-color: yellow;width: 100%;display: flex; flex-direction: row;">
        <el-color-picker 
        v-for="(item,index) in dpis" 
        :key="index"
        v-model="item.color" 
        size="mini"></el-color-picker>
    </div> -->
  </div>
</template>

<script>
  import HIDHandle from './HIDHandle';
  import DpiRadio from '../components/DpiRadio'

  export default {
    components: {
      DpiRadio
    },
    data() {
      return {
        loadFinish:true,
        colors:[
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
        ],
        dpiIndex: 0,
        dpiValue:400,
        maxDPI:1,
        dpis: [
          {
            value:400,
            color:"#F00"
          },
          {
            value:800,
            color:"#0F0"
          },   
          {
            value:1600,
            color:"#FFF"
          },
          {
            value:3200,
            color:"#FFF"
          },  
          {
            value:6400,
            color:"#FFF"
          },
          {
            value:8000,
            color:"#FFF"
          },   
          {
            value:10000,
            color:"#FFF"
          },
          {
            value:12000,
            color:"#FFF"
          },        
        ]
      }
    },
    methods: {
      ChangeDPIValue() {
        this.dpis[this.dpiIndex].value = this.dpiValue;
      },
      async UpdateDPIIndex() {
        await HIDHandle.setCurrentDPI(this.dpiIndex);
      },
      async changeMaxPDI() {
        console.log(this.maxDPI+ 1);
        await HIDHandle.setMaxDPI(this.maxDPI+ 1);
      },
      async UpdateColor(value) {
        let hex = value[1].slice(1).split('');
        let rgb = [
          parseInt(hex.splice(0, 2).join(''), 16), // 取两个字符并转换为10进制，然后添加到数组
          parseInt(hex.splice(0, 2).join(''), 16),
          parseInt(hex.splice(0, 2).join(''), 16)
        ];
        await HIDHandle.setDPIColor(rgb, Number.parseInt(value[0]));
      }
    },
    mounted() {
      this.$bus.$on('getDeviceInfo',value => {
        this.loadFinish = true;
        this.dpis = value.DPIs;
        console.log('DPIs',this.dpis);
      });

      this.$bus.$on('DpiUpdateIndex',value => {
        this.dpiIndex = value;
        this.dpiValue =  Number.parseInt(this.dpis[this.dpiIndex].value);
        console.log('dpiIndex',this.dpiIndex);
        this.UpdateDPIIndex();
      });

      this.$bus.$on('DpiUpdateColor',value => {
        console.log('DpiUpdateColor',value);
        this.UpdateColor(value);
      });
    },
    beforeDestroy() {
      this.$bus.$off('getDeviceInfo');
      this.$bus.$off('DpiUpdateIndex');
      this.$bus.$off('DpiUpdateColor');
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  margin-left: 20px;
}

select {
  width: 50px;
  height: 20px;
  margin: auto;
  margin-left: 10px;
}

.el-slider {
  width: 80%;
  margin: auto;
}

</style>