<template>
  <div v-show="loadFinish">
    <span style="margin-left:20px">Sensor设置</span>
    <div style="display: flex;flex-direction: row;">
      <div class="sensorMode">
        <span>模式选择</span>
        <select name="sensorMode">
          <option 
          v-for="(item,index) in sensorModes"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div class="lod">
        <span>LOD高度</span>
        <select name="lod">
          <option 
          v-for="(item,index) in lods"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div class="performance">
        <el-checkbox v-model="performance">火力全开</el-checkbox>
        <select name="performance">
          <option 
          v-for="(item,index) in performances"
          :key="index" 
          :value="item.value">
          {{ item.option }}
        </option>
        </select>
      </div>

      <div class="sensorSetting">
        <div class="ripple">
          <el-switch
            v-model="rippleState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="rippleChange">
          </el-switch>
          <span>波纹修正</span>
        </div>
        <div class="fixLine">
          <el-switch
            v-model="fixLineState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="rippleChange">
          </el-switch>
          <span>直线修正</span>
        </div>
        <div class="motionSync">
          <el-switch
            v-model="motionSyncState"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="rippleChange">
          </el-switch>
          <span>Motion Sync</span>
        </div>
      </div>    
    </div>
  </div>
 
</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    data() {
      return {
        loadFinish:false,
        performance:false,
        rippleState:false,
        fixLineState:false,
        motionSyncState:false,
        sensorModes:[],
        lods:[],
        performances:[],
      }
    },
    methods: {
      async rippleChange() {
        await HIDHandle.setRipple(this.rippleState ? 1 : 0);
      },
      async fixLineChange() {
        await HIDHandle.setFixLine(this.fixLineState ? 1 : 0);
      },
      async motionSyncChange() {
        await HIDHandle.setMotionSync(this.motionSyncState ? 1 : 0);
      }
    },
    mounted() {
      this.$bus.$on('getLanFiles',value => {
        this.sensorModes = value[1].SensorModeSelectOption;
        this.lods = value[1].LODOption;
        this.performances = value[1].PerformanceOption;
        console.log('Sensor',this.lods);
        this.loadFinish = true;
      });
    },
    beforeDestroy() {
      this.$bus.$off('getLanFiles');
    }
  }
</script>

<style lang="less" scoped>
span {
  font-size: 16px;
  display: flex;
  align-items: flex-start;
}

.sensorMode, .lod, .performance {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

select {
  margin-top:40px;
  position: absolute; 
  font-size: 12px; 
  width: 120px; 
  height: 25px; 
  text-align: center;
}

.sensorSetting {
  margin-top: 30px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.ripple, .fixLine, .motionSync {
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/deep/ .el-checkbox__input.is-checked  {
  .el-checkbox__inner {
    background-color: #ff7f00;
    border-color: #ff7f00;
  }
}

/deep/ .el-checkbox .el-checkbox__label {
    color:#FFF;
  }
</style>